
import { api_login, api_signin } from '@/api/user'
import { useStore } from 'vuex'
import { useRouter } from 'vue-router'
import { defineComponent, onMounted, reactive, toRefs } from 'vue'
import { message } from 'ant-design-vue'
import type { UploadChangeParam, UploadProps } from 'ant-design-vue'

export default defineComponent({
  name: 'ChatLogin',
  setup() {
    const store = useStore()
    const router = useRouter()
    const state = reactive({
      loginInfo: {
        userName: '',
        password: '',
      },
      registerInfo: {
        avatar: '',
        userName: '',
        password: '',
        confirmPassword: '',
        email: '',
      },
      isLogin: true,

      imgLoading: false,
    })

    onMounted(() => {
      if (store.state.userInfo) {
        router.go(0)
      }
      window.$socket && window.$socket.disconnect()
    })

    // 点击登录的回调
    const login = async () => {
      // 判断是否存在空值
      for (var key in state.loginInfo) {
        if (!state.loginInfo[key as keyof typeof state.loginInfo].trim()) {
          message.warning(`${key}不能为空哦`)
          return
        }
      }

      let res = await api_login(state.loginInfo)
      if (res) {
        store.commit('setUserInfo', res.userInfo)
        router.push('/index')
      }
    }

    // 点击注册的回调
    const signin = async () => {
      // 判断是否存在空值
      for (var key in state.registerInfo) {
        if (!state.registerInfo[key as keyof typeof state.registerInfo].trim()) {
          message.warning(`${key}不能为空哦`)
          return
        }
      }

      if (state.registerInfo.password !== state.registerInfo.confirmPassword) {
        message.warning('两次密码不一致')
        return
      }

      // 正则校验邮箱
      let emailReg = /^[A-Za-z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/
      if (!emailReg.test(state.registerInfo.email)) {
        message.warning('邮箱格式错误')
        return
      }

      let res = await api_signin(state.registerInfo)

      if (res) {
        message.success('注册成功')
        state.isLogin = true
        state.loginInfo = {
          userName: state.registerInfo.userName,
          password: '',
        }
      }
    }

    const handleChange = (info: UploadChangeParam) => {
      if (info.file.status === 'uploading') {
        state.isLogin = true
        return
      }
      if (info.file.status === 'done') {
        state.registerInfo.avatar = info.file.response.src
        state.imgLoading = false
      }
    }

    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
    // @ts-ignore
    const beforeUpload = (file: UploadProps['fileList'][number]) => {
      const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png'
      if (!isJpgOrPng) {
        message.error('只能上传 JPG/PNG 格式的文件哦')
      }
      const isLt2M = file.size / 1024 / 1024 < 2
      if (!isLt2M) {
        message.error('图片大小不能超过2M')
      }
      return isJpgOrPng && isLt2M
    }

    return {
      ...toRefs(state),
      login,
      signin,
      handleChange,
      beforeUpload,
    }
  },
})
